import React, { useLayoutEffect, useRef } from 'react';
import { Input } from '@chakra-ui/react';
import Inputmask from 'inputmask';

type Props = {
    value: string;
    // eslint-disable-next-line no-unused-vars
    onChange: (maskedValue: string, unmaskedValue: string, isValid: boolean) => void;
}

const mask = '9{6,17}';

const AccountNumberInput:React.FC<Props> = ({
  onChange, value,
}) => {
  const input = useRef(null);

  useLayoutEffect(() => {
    if (input.current) {
      Inputmask({
        mask,
        showMaskOnHover: false,
        showMaskOnFocus: false,
        jitMasking: true,
      }).mask(input.current);
    }
  }, [mask]);

  return (
  // eslint-disable-next-line react/jsx-props-no-spreading
    <Input
      data-testid="ssn-input"
      variant="primary"
      ref={input}
      onChange={(e:any) => {
        const v = e.target.value;
        const isValid = Inputmask.isValid(v, { mask });
        const unmaskedValue: string = (input.current as any)?.inputmask.unmaskedvalue();
        onChange(v, unmaskedValue, isValid);
      }}
      value={value}
    />
  );
};

export default AccountNumberInput;
