import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { achPay } from '../../../store/middlewares/payment';
import { updateContact } from '../../../store/middlewares/contact';
import { apiErrorHandler } from '../../../utils/general';
import { useModuleData, useModulePageData } from '../../../hooks/module';
import { sendMessage } from '../../../utils/sdk';
import { Data } from '../../../modules/Payments';
import { AchData } from '../../../modules/Payments/Ach';
import { COMMON } from '../../../const/messages';
import CurrencyInput from '../../../components/CurrencyInput';
import RoutingNumberInput from '../../../components/RoutingNumberInput';
import AccountNumberInput from '../../../components/AccountNumberInput';

const accountTypes = {
  personalSavings: 'Personal Savings',
  personalChecking: 'Personal Checking',
  businessSavings: 'Business Savings',
  businessChecking: 'Business Checking'
};

const Ach:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  const {
    startScreen = 'options',
  } = useModuleData<Data<'ach'>>();

  const { showBack } = useModulePageData<AchData, Data<'ach'>['startScreen']>('ach');

  const accountDetails = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const [localContactDetails, updateContactData] = useState({
    name: contactDetails?.name || '',
    accountNumber: contactDetails?.ach?.accountNumber || '',
    routingNumber: contactDetails?.ach?.routingNumber || '',
    bankName: contactDetails?.ach?.bankName || '',
    accountType: contactDetails?.ach?.accountType || 'personalSavings'
  });

  const [paymentData, updatePaymentData] = useState({
    amount: '',
    description: '',
    contactId: contactDetails?.id || '',
    accountId: accountDetails?.id || ''
  });

  const isValid = useMemo(() => localContactDetails.name.length !== 0 && localContactDetails.accountNumber.length !== 0 && localContactDetails.accountType.length !== 0 && localContactDetails.routingNumber.length !== 0 && localContactDetails.bankName.length !== 0 && paymentData.amount.length !== 0 && parseFloat(paymentData.amount) >= 0.01 && paymentData.description.length !== 0, [localContactDetails, paymentData]);

  const onSubmit = useCallback(() => {
    if (contactDetails?.id && isValid) {
      dispatch(updateContact(contactDetails.id, {
        accountId: contactDetails.accountId,
        name: localContactDetails.name,
        ach: {
          accountNumber: localContactDetails.accountNumber,
          routingNumber: localContactDetails.routingNumber,
          bankName: localContactDetails.bankName,
          accountType: localContactDetails.accountType
        }
      })).then(() => {
        dispatch(achPay(paymentData))
          .then(() => {
            navigate('/payments/success');
          }).catch(apiErrorHandler);
      }).catch(apiErrorHandler);
    }
  }, [isValid, paymentData, localContactDetails]);

  return (
    <div className="page">
      <MiniBox
        heading={t('payments_ach.title')}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={onSubmit}>
            {t('payments_ach.ctaText')}
          </Button>
)}
        showBack={startScreen !== 'ach' || !!showBack}
        onBack={() => {
          sendMessage(COMMON.BACK_PRESSED, { from: 'ach' });
          if (startScreen !== 'ach') {
            navigate(-1);
          }
        }}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.name')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails?.name || ''}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  name: e.target.value
                });
              }}
            />
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.accountNumber')}
            </FormLabel>
            <AccountNumberInput
              value={localContactDetails.accountNumber}
              onChange={(_, v) => {
                updateContactData({
                  ...localContactDetails,
                  accountNumber: v

                });
              }}
            />
            {/* <Input
              variant="primary"
              value={localContactDetails.accountNumber}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  accountNumber: e.target.value

                });
              }}
            /> */}
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.routingNumber')}
            </FormLabel>
            <RoutingNumberInput
              value={localContactDetails.routingNumber}
              onChange={(_, v) => {
                updateContactData({
                  ...localContactDetails,
                  routingNumber: v

                });
              }}
            />
            {/* <Input
              variant="primary"
              value={localContactDetails.routingNumber}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  routingNumber: e.target.value

                });
              }}
            /> */}
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.accountType')}
            </FormLabel>
            <Select
              variant="primary"
              value={localContactDetails.accountType}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  accountType: e.target.value

                });
              }}
            >
              {Object.keys(accountTypes).map((a) => <option key={a} value={a}>{(accountTypes as any)[a]}</option>)}

            </Select>
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.bank')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails.bankName}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  bankName: e.target.value

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.amount')}
            </FormLabel>
            <CurrencyInput
              value={paymentData.amount}
              onChange={(value) => {
                updatePaymentData({
                  ...paymentData,
                  amount: value || ''
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_ach.purpose')}
            </FormLabel>
            <Input
              variant="primary"
              value={paymentData.description}
              onChange={(e) => {
                updatePaymentData({
                  ...paymentData,
                  description: e.target.value
                });
              }}
            />
          </FormControl>
        </Box>
      </MiniBox>
    </div>
  );
};

export default Ach;
